import { useContext, useEffect, useState } from 'react';
import { Container, Card, CardContent, Typography, Grid, Box, CircularProgress } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { SettingsContext } from '../../context/SettingsContext';
import { useUser } from '../../context/UserContext';

const BlogCard = ({ sx }) => {
    const { language, themeMode } = useContext(SettingsContext);
    const { userState } = useUser();
    const [blogs, setBlogs] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const navigate = useNavigate();

    const filterVisibleBlogs = (blogList) => {
        const isStaff = userState.is_staff; 
        if (isStaff) {
            return blogList; 
        } else {
            return blogList.filter(blog => blog.visible !== false);
        }
    };

    // 加载博客元数据
    useEffect(() => {
        const fetchBlogsMetadata = async () => {
            try {
                setLoading(true);
                setError(null);
                
                const response = await fetch('/Blogs/blogs_metadata.json');
                if (!response.ok) {
                    throw new Error(`HTTP error! status: ${response.status}`);
                }
                
                const metadata = await response.json();
                // 在这里过滤博客数据
                const filteredBlogs = filterVisibleBlogs(metadata);
                setBlogs(filteredBlogs);
                setLoading(false);
            } catch (error) {
                console.error('Error fetching blogs metadata:', error);
                setError(language === 'cn' ? '404 \n 服务器访问失败 \n 无法加载博客' : '404 \n Internal Server Error \n Fail to load blogs');
                setLoading(false);
            }
        };
        
        fetchBlogsMetadata();
    }, [language, userState.is_staff]); // 添加 userState.is_staff 作为依赖

    const handleCardClick = (id) => {
        navigate(`/blog/${id}`);
    };

    return (
        <Container maxWidth="md" sx={{ ...sx }}>
            {loading ? (
                <Box display="flex" justifyContent="center" mt={3.5}>
                    <CircularProgress />
                </Box>
            ) : error ? (
                <Typography variant="h6" color="error" align="center" mt={3.5} style={{ whiteSpace: "pre-line" }}>
                    {error}
                </Typography>
            ) : (
                <>
                    <Grid container spacing={2} direction="column">
                        {blogs.map((blog) => (
                            <Grid item key={blog.id}>
                                <Card
                                    sx={{
                                        background: 'transparent',
                                        borderBottom: `1px solid ${themeMode === 'light' ? '#ccc' : '#555'}`,
                                        borderRadius: 0,
                                        boxShadow: 'none',
                                        cursor: 'pointer',
                                    }}
                                    onClick={() => handleCardClick(blog.id)}
                                >
                                    <CardContent>
                                        <Typography
                                            variant="h5"
                                            component="div"
                                            gutterBottom
                                            sx={{ fontFamily: 'Georgia, serif', fontWeight: 'bold' }}
                                        >
                                            {language === 'cn' ? blog.cn_title : blog.en_title}
                                        </Typography>
                                        <Typography
                                            variant="body1"
                                            color="text.primary"
                                            sx={{ fontFamily: 'Georgia, serif' }}
                                        >
                                            {language === 'cn' ? blog.cn_summary : blog.en_summary}
                                        </Typography>
                                        <Box display="flex" justifyContent="flex-end" mt={2} sx={{ fontFamily: 'Georgia, serif' }}>
                                            <Typography variant="body1" color="text.secondary">
                                                {
                                                new Date(blog.publish_date).toLocaleDateString(language === 'cn' ? 'zh-CN' : 'en-GB', {
                                                    day: '2-digit',
                                                    month: language === 'cn' ? '2-digit' : 'short',
                                                    year: 'numeric',
                                                    })
                                                }
                                            </Typography>
                                        </Box>
                                    </CardContent>
                                </Card>
                            </Grid>
                        ))}
                    </Grid>
                    
                    {/* 显示总数信息 */}
                    <Box display="flex" justifyContent="center" mt={2} mb={2}>
                        <Typography variant="body" color="text.secondary" sx={{ fontFamily: 'Georgia, serif' }}>
                            {language === 'cn' 
                                ? `共 ${blogs.length} 篇文章`
                                : `${blogs.length} articles total`
                            }
                        </Typography>
                    </Box>
                </>
            )}
        </Container>
    );
};

export default BlogCard;